


















































.confirm[_v-07f8688c]{
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(0,0,0,.2);
	z-index: 1002
}
.confirm-body[_v-07f8688c]{
	padding: 15px 15px 0;
}
.confirm-dialog[_v-07f8688c]{
	width: 50%;
	min-width: 250px;
	max-width: 360px;
	margin: 0 auto;
	position: absolute;
	top: 50%;
	left: 50%;
	/*padding: 15px;*/
	border-radius: 5px;
	background-color: #fff;
	-webkit-transform: translate(-50%, -50%) scale(1);
	        transform: translate(-50%, -50%) scale(1);
	-webkit-transition: all .3s;
	transition: all .3s
}
.confirm-footer[_v-07f8688c]{
	border-top: 1px solid rgba(0,0,0,.2)
}

.cancelBtn[_v-07f8688c]:after{
	content: "";
	display: block;
	position: absolute;
	right: 0;
	top: 0;
	bottom: 0;
	width: 1px;
	background-color: rgba(0,0,0,.2);
}
.cancelBtn[_v-07f8688c],.confirmBtn[_v-07f8688c]{
	width: 50%;
	float: left;
	text-align: center;
	line-height: 36px;
	position: relative;
	cursor: pointer;
}

.confirm-transition[_v-07f8688c]{
	-webkit-transition: all .3s;
	transition: all .3s
}
.confirm-enter[_v-07f8688c], .confirm-leave[_v-07f8688c] {
  opacity: 0;
}

.confirm-enter .confirm-dialog[_v-07f8688c],
.confirm-leave .confirm-dialog[_v-07f8688c] {
  -webkit-transform: translate(-50%, -50%) scale(1.1);
  transform: translate(-50%, -50%) scale(1.1);
}




